.metronome {
    text-align: center;
    max-width: 350px;
    margin: 0 auto;
    padding: 30px;
}

.bpm-slider input {
    width: 100%;
    margin: 10px;
}

.metronome button {
    background: #c94d46;
    padding: 10px;
    border: 1px solid #832420;
    width: 100px;
    border-radius: 2px;
    font-size: 18px;
    color: #fff;
}